* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
body {
    color: #333;
    background-color: #f5f5f5;
}
.memo {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
    // border: 1px solid #333; 
    padding: 0 15px;
    .input_text {
        position: relative;
        
        input[type="text"] {
            width: 100%;
            height: 50px;
            border: 1px solid #999;
            border-radius: 40px;
            padding: 0 10px;
            
        }
        button {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: 0;
            position: absolute;
            right: 5px;
            top: 50%;
            transform: translateY(-50%);
            background-color: #2D45ED;
            color: #fff;
        }
    }
    .list__text {
        margin-top: 20px;
        border-top: 1px solid #ccc;
        padding-top: 20px;
    }
    .item {
        display: flex;
        justify-content: space-between;
        align-items: center;
       
        background-color: #fff;
        margin: 10px 0;
        padding: 12px 20px;
        border-radius: 10px;
        

        input[type="checkbox"] {
            display: none; 
            & + .item_info {
                position: relative;
                padding-left: 40px;
                strong {
                    // font-weight: normal;
                    font-size: 18px;
                }
            }
            & + .item_info::before {
                content: '';
                background: url('img/check-default.svg') center center / 100% no-repeat;
                width: 30px;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
            }
            &:checked + .item_info::before {
                background-image: url('img/check-active.svg');
            }
            &:checked + .item_info strong {
                text-decoration: line-through;
            }
        }
        &_info {
            
        }
        &_date {
            display: flex;
            font-size: 13px;
        }
        .text_remove {
            width: 30px;
            height: 30px;
            img {
                width: 100%;
            }
        }
        


    }/* .item end */
}/* .memo end */